import React, { Component } from 'react'
import FreePassButton from '../FreePassButton/FreePassButton'
import './Header.styl'

class Header extends Component {
  render() {
    return (
      <div className="header">
        <div className='headline'>
          <h2>{this.props.data.headline}</h2>
        </div>
        <div className='header-content'>
          <div className='video'>
            <video id="wistia_simple_video_26" crossOrigin="anonymous" preload="metadata" type="video/m3u8" poster="https://fast.wistia.com/assets/images/blank.gif" playsInline="playsinline" src="https://recoverywarriors.wistia.com/144fcb5e-1094-4b0d-a9e8-17c6d471532d" style={{background: 'transparent', display: 'block', height: '100%', position: 'static', visibility: 'visible', width: '100%', objectFit: 'contain'}}></video>
          </div>
          <div className='description'>
            <p>{this.props.data.description}</p>
            <p>{this.props.data.offer}</p>
            <div className='ticket'>
              <img src={require('../../img/header-ticket.png')} />
              <p>Grab your free pass to the FoodBodyLove Summit!</p>
            </div>
          </div>
        </div>

        <FreePassButton openEmailOptIn={this.props.openEmailOptIn}/>
      </div>
    );
  }
}

export default Header;

